import viewAllProjects from "./viewAllProjects";
import createNewProject from "../../Logic/createNewProject";
import Project from "../../Models/Project";
import myTodos from "../../Logic/myTodos";
import updateStorage from "../../Logic/updateStorage";

const addsNewProjectToUI = () => {
  const addProjectForm = document.querySelector("#add-project-form");
  const projectsBox = document.querySelector(".projects-box");
  const name = document.querySelector("#name");
  addProjectForm.addEventListener("submit", () => {
    const newProject = createNewProject(new Project(name.value));
    newProject.setTodos(myTodos);
    updateStorage()
    addProjectForm.reset();
    projectsBox.innerHTML = "";
    viewAllProjects();
  });
};

export default addsNewProjectToUI;
